.sign-up{
  display: flex;
  margin: 80px 0;
  justify-content: space-around;
  .manual--sign-up{
    flex: 0 0 40%;
    .sign-up__header{
      color: $colorBlue;

    }
    .sign-up-form{
      .form-group{
        margin: 25px 0 ;
        .form-control{
          border: 0;
          border-bottom: 2px solid #aaa;
          box-shadow: none;
          &:focus{
            border-bottom: 2px solid $colorBlue;
          }
        }
        .sign-me-up-btn{
          background-color: $colorYellow;
          color: $colorBlue;
          padding: 6px 40px;
          margin: 20px 0;
        }
      }
    }
  }
  .or{
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 10%;
    span{
      background-color: #eee;
      border-radius: 50%;
      padding: 15px;
    }
  }
  .social--sign-up{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 0 0 40%;
    .login{
      margin: 15px 0;
      .login-btn{
        width: 100%;
        padding: 8px 40px;
        color: $colorWhite;
      }
      .facebook-login-btn{
        background-color: #4267B2;
        
      }
      .twitter-login-btn{
        background-color: #1DA1F2;
      }
      .google-login-btn{
        background-color: #DB4437
      }
    }

  }
}
@media only screen and (max-width:768px) {
  .sign-up{
    flex-direction: column;
  }
}